import React, {Component, Fragment} from 'react';
import gql from 'graphql-tag';
import { Query } from 'react-apollo';
import '../styles/text.css'
// https://developer.aliyun.com/mirror/npm/package/classnames
import classNames from 'classnames'
import {Link} from 'react-router-dom'


const LAUNCH_QUERY = gql`
    query LaunchQuery ( $flight_number: Int!) {
        launch(flight_number: $flight_number) {
            flight_number
            mission_name
            launch_year
            launch_success
            launch_date_local,
            rocket { 
                rocket_id
                rocket_name
                rocket_type
            }
        }
    }
`;


export class Launch extends Component {
    render() {
        let { flight_number } = this.props.match.params;
        flight_number = parseInt(flight_number);
        return (
            <Fragment>
                <Query query={LAUNCH_QUERY} variables={{flight_number}}>
                    {
                        ({ loading, error, data }) => {
                            if(loading) return <h4>loading...</h4>
                            if(error) console.log(error);

                            const { mission_name, flight_number, launch_year, launch_success, 
                                rocket: {rocket_id, rocket_name, rocket_type}} = data.launch;
                            return <Fragment>
                                <h1>
                                    Mission: {mission_name}
                                </h1>
                                <h4>Launch Details:</h4>
                                <ul>
                                    <li>Flight Number: {flight_number}</li>
                                    <li>Launch Year: {launch_year}</li>
                                    <li>Launch Success: <span className={classNames({
                                        'success': launch_success,
                                        'fail': !launch_success
                                    })}>{launch_success ? 'yes' : 'no'}</span> </li>
                                </ul>

                                <h4>Rocket Details:</h4>
                                <ul>
                                    <li>Rocket ID: {rocket_id}</li>
                                    <li>Rocket Name: {rocket_name}</li>
                                    <li>Rocket Type: {rocket_type} </li>
                                </ul>
                                <Link to={`/carpentry`} ><h4>Back</h4></Link>
                            </Fragment>;
                        }
                    }
                </Query>
            </Fragment>
        )
    }
}

export default Launch